.element__button-like {
  width: 48px;
  height: 48px;
  border: none;
  border-radius: 4px;
  background: url(../../../images/like.svg) no-repeat center;
  margin: 15px 10px 0 0;
  transition: 0.3s;
}

.element__button-like:hover {
  opacity: 0.6;
  cursor: pointer;
}

.button-like_yes {
  background: url(../../../images/like-solid.svg) no-repeat center;
}
